
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.stars {
  /* animation: App-stars infinite 20s linear; */
}

.app {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
}

.main-text {
  font-family: 'Palanquin', sans-serif;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: #212121;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
  /* border: 1px solid #000; */
  transition: 100ms;
  /* background-color: rgba(0,0,0,0); */
}

.main-text h1 {
  font-size: 55px;
  padding: 0px;
  margin: 0px;
}

.main-text h2 {
  font-size: 21px;
  padding: 0px;
  margin: 0px;
  color: #212121;
}

.main-text p {
  color: #00bcd4;
}

.links-modal {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.links-btn {
  padding: 10px;
  width: 150px;
  height: 25px;
  background: #fff;
  box-shadow: 0 0 2px #bdbdbd;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-right: 10px;
  text-decoration: none;
  color: #0097a7;
  font-size: 13px;
  font-weight: bold;
  line-height: 25px;
  transition: 250ms;
}

.links-btn:hover {
  /* box-shadow: 0 0 3px #424242; */
}

.links-btn:active {
  /* box-shadow: 0px 0 1px #42424240; */
}

.projects-wrapper {
  position: absolute;
  bottom: 0;
  height: 60vh;
  /* background-color: #21212150; */
  display: none;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  box-sizing: border-box;
  transition: 100ms;
  opacity: 0;
/*  */
}

.project-card {
  position: relative;
  width: 285px;
  height: 180px;
  background-color: #ffffff00;
  border-radius: 10px;
  margin-top: 10px;
  box-shadow: 0 0 2px #bdbdbd;
  overflow: hidden;
}

.project-img {
  position: absolute;
  width:auto;
  height: 100%;
  top: 0;
  left: 0;
}

.project-details {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  transition: 200ms;
  opacity: 0;
}
.project-details:hover {
  opacity: 1;
}


.project-title {
  position: relative;
  width: 100%;
  padding: 0;
  padding-left: 20px;
  font-size: 21px;
  /* line-height: 25px; */
  margin: 0;
  background-color: rgba(255,255,255,1);
  font-family: 'Palanquin', sans-serif;
  font-weight: 900;
  color: #212121;
  box-shadow: 0 0 1px #bdbdbd;
}

.project-link {
  background-color: rgba(255,255,255,1);
  width: 100%;
  padding: 0;
  padding-left: 20px;
  padding-bottom: 20px;
  font-size: 17px;
  line-height: 17px;
  margin: 0;
  position: relative;
  text-decoration: none;
  font-family: 'Palanquin', sans-serif;
  font-weight: 900;
  color: #0097a7;
}

.project-tech-wrapper {
  position: relative;
}

.project-tech {

}


* {
  /* border: 1px solid black; */
}

@media only screen and (max-width: 1070px) {
  .projects-wrapper {
    overflow: scroll;
    padding-bottom: 20px;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-stars {
  from {
    right: 0;
  }
  to {
    right: 100%;
  }
}
